<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 3
      h1 La comunicacin comercial

    .row.justify-content-center.align-items-center
      .col-lg-8.col-md-12.col-sm-12
        p Es el grupo de instrumentos de comunicacin que usa la empresa de forma masiva o personalizada para hacer promocin de una marca, producto o servicio, con el fin de llamar la atencin del pblico objetivo al que van dirigidos (Wolters, 2021).
        .cajon.color-acento-contenido.p-4.mb-4
          p La comunicacin es uno de los cuatro instrumentos bsicos del mercadeo, que se clasifican tradicionalmente en publicidad, relaciones pblicas, promocin de ventas y venta personal. En la actualidad se han sumado otras dos, el #[em buzz marketing] o el #[em marketing]  experiencial. Por eso, hoy se suele hablar de comunicacin 360 o Comunicacin Integrada de #[em Marketing] (CIM). La idea es que ayude a lograr los objetivos de #[em marketing] a travs de la comunicacin, buscando la eficacia en las estrategias y su respectivo plan de accin, conociendo lo mejor posible al cliente objetivo e impactndolo a travs de todos los medios que sean posibles (segn el msculo financiero de la marca) y con una estrategia envolvente.
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema3/img1.png', alt='')

    separador
    #t_3_1.titulo-segundo.mb-5
      h2 3.1 Niveles la comunicacin comercial

    p Los instrumentos o herramientas de comunicacin son aplicados a diferentes niveles dentro de la empresa, buscando diferentes objetivos segn sea el nivel donde se aplique:

    .row(data-aos="fade-down")
      .col-lg-4.col-md-12.col-12
        .tarjeta-avatar
          img.mt-5(src='@/assets/curso/temas/tema3/tarj1-1.png' alt='')
          .row.justify-content-center.align-items-center.mt-5.mb-5.mx-1
            .tarjeta--boton.tema1
              .p-4
                .row.mt-5
                .row.mt-5
                h4.text-center.mt-5 Nivel corporativo
                p.text-center.mb-5 El objetivo de comunicacin es construir , defender y mantener la reputacin de la marca.
                .row.mb-5
                .row.mb-5
      .col-lg-4.col-md-12.col-12
        .tarjeta-avatar
          img.mt-5(src='@/assets/curso/temas/tema3/tarj1-2.png' alt='')
          .row.justify-content-center.align-items-center.mt-5.mb-5.mx-1
            .tarjeta--boton.tema1
              .p-4
                .row.mt-5
                .row.mt-5
                h4.text-center.mt-5 Nivel de marca
                p.text-center.mb-5 El propsito es facilitar la comercializacin de productos y servicios, as como construir una imagen de  marca. Esta imagen de marca representa la percepcin que el cliente tiene de la empresa.
                .row.mb-5
      .col-lg-4.col-md-12.col-12
        .tarjeta-avatar
          img.mt-5(src='@/assets/curso/temas/tema3/tarj1-3.png' alt='')
          .row.justify-content-center.align-items-center.mt-5.mb-5.mx-1
            .tarjeta--boton.tema1
              .p-4
                .row.mt-5
                .row.mt-5
                h4.text-center.mt-5 Nivel interno
                p.text-center.mb-4 El fin es construir una identidad hacia el interior de la empresa en la que se estimule la satisfaccin, la moral, el rendimiento y el talento de los empleados. Se le conoce como identidad interna compartida que ayuda emocionalmente a mantener unida la empresa. El responsable es el rea de recursos humanos.

    .mb-4.d-flex.ps-lg-0
      .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
        h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") Etapas de un plan de comunicacin comercial
        img.icon(src='@/assets/curso/temas/icon.svg') 

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema3/img2.png', alt='')
      .col-lg-8.col-md-12.col-sm-12
        p.mb-2 Un plan de comunicacin usado para cualquier nivel debe pasar por las siguientes etapas:
        .row.justify-content-center.align-items-center.bgazulclaro.borde-redondo
          .col-lg-12.col-md-12.col-sm-12
            .row.justify-content-center.align-items-center.mt-4
              .col-lg-1.col-md-2.col-4
                img(src='@/assets/curso/temas/tema3/ico1.png', alt='')
              .col-lg-10.col-md-12.col-sm-12
                p El plan inicia  con el diagnstico general de la empresa y la identificacin del problema que esta tiene actualmente.
            .row.justify-content-center.align-items-center
              .col-lg-1.col-md-2.col-4
                img(src='@/assets/curso/temas/tema3/ico2.png', alt='')
              .col-lg-10.col-md-12.col-sm-12
                p Luego de desarrollar este anlisis en la etapa inicial se pasa a la segunda etapa donde definen los objetivos: lo que se desea lograr con la comunicacin. 
            .row.justify-content-center.align-items-center
              .col-lg-1.col-md-2.col-4
                img(src='@/assets/curso/temas/tema3/ico3.png', alt='')
              .col-lg-10.col-md-12.col-sm-12
                p La tercera etapa se enfoca en identificar el pblico objetivo al que va dirigida la comunicacin . Adems, en esta fase se establecen la estrategia del mensaje (qu y cmo se desea comunicar) y la estrategia de medios (qu canales se emplearn).
            .row.justify-content-center.align-items-center.mb-4
              .col-lg-1.col-md-2.col-4
                img(src='@/assets/curso/temas/tema3/ico4.png', alt='')
              .col-lg-10.col-md-12.col-sm-12
                p Finalmente, se debe adjudicar un presupuesto que sirva para conseguir los objetivos trazados. Adems de disear un cronograma donde se ordene bien la planeacin, los objetivos, las estrategias, las acciones, los responsables y las fechas de realizacin de cada accin, de tal forma que el equipo de trabajo revise, semana tras semana, cules son las actividades para desarrollar.

    .mb-4.d-flex.ps-lg-0
      .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
        h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") Canales y tipos de contacto
        img.icon(src='@/assets/curso/temas/icon.svg') 

    p Canal o medio de contacto es la ruta por la que un cliente interesado en una empresa puede entrar en contacto con ella. Algunos de ellos permiten un contacto bidireccional con el cliente utilizando medios como: redes sociales, chat, blog, telfono, etc. Otros no los utilizan como, por ejemplo, los medios masivos: televisin, prensa, revistas, etc. 
    p.mb-4 En este apartado de canales y tipos de contacto, se refiere a aquellos medios en los que la empresa le da la oportunidad al cliente de tener una conversacin o una interaccin. As entonces, las empresas en la actualidad eligen preferiblemente diferentes canales, en los que pueda existir ese contacto bidireccional con sus clientes, as:

    .row.justify-content-center.align-items-center
      .col-lg-12.col-md-12.col-12
        LineaTiempoD.color-secundario
          .row.align-items-center.justify-content-center(numero="1" titulo= "Telfono")
            .col-md-10.col-lg-7.col-sm-12
              p Es considerado el ms tradicional por excelencia y sin importar la aparicin de nuevas tecnologas contina siendo uno de los medios ms usados. Este medio entrega muchas ventajas, como, por ejemplo, es inmediato, rpido, permite un contacto bidireccional y una fluidez en la comunicacin. Como desventaja, es que le falta, un contacto  ms en directo, persona a persona. La mayora de las empresas lo tienen entre sus canales preferidos para contactar a sus clientes. 
            .col-md-2.col-lg-4.col-4
              img(src='@/assets/curso/temas/tema3/ps1-1.png', alt='')
          .row.align-items-center.justify-content-center(numero="2" titulo= "Correo electrnico")
            .col-md-10.col-lg-7.col-sm-12
              p Una de las vas que ms adeptos ha recabado desde los inicios de la red. Es muy verstil en cuanto al reparto de tiempos (se puede responder de inmediato, pero tambin dejar pasar un tiempo, si no se quiere hacerlo de inmediato). Tiene un componente de "formalidad" que otorga el formato por escrito y que se puede archivar con facilidad. El correo electrnico es, sin lugar a duda, uno de los favoritos tanto para clientes como para empresas.
            .col-md-2.col-lg-4.col-4
              img(src='@/assets/curso/temas/tema3/ps1-2.png', alt='')
          .row.align-items-center.justify-content-center(numero="3" titulo= "Formulario de contacto")
            .col-md-10.col-lg-7.col-sm-12
              p Es un medio bastante til, porque es primordial para interactuar con los clientes y poderles ofrecerles una manera veloz y sin problemas de mantener un contacto directo con la empresa. Su funcin es la de recibir el primer contacto con un cliente potencial, que por lo general se encuentra ubicado en el sitio web, y que a partir de ah comienza  la venta, por lo que es una pieza vital en el engranaje para entregar valor a la marca.
            .col-md-2.col-lg-4.col-4
              img(src='@/assets/curso/temas/tema3/ps1-3.png', alt='')
          .row.align-items-center.justify-content-center(numero="4" titulo= "Redes sociales")
            .col-md-10.col-lg-7.col-sm-12
              p Estas plataformas se usan como medios de contacto personalizado y resultan bastante tiles. La empresa debe saber cules redes sociales usar. La sugerencia es tener pocas redes sociales y que sean aquellas donde el cliente potencial se encuentra presente.
            .col-md-2.col-lg-4.col-4
              img(src='@/assets/curso/temas/tema3/ps1-4.png', alt='')
          .row.align-items-center.justify-content-center(numero="5" titulo= "<i>Chatbot</i>")
            .col-md-10.col-lg-7.col-sm-12
              p Herramienta de #[em software] que reemplaza las funciones de una persona y que a travs de una interfaz tipo chat puede crear conversaciones en tiempo real con los clientes de la marca. Han ganado popularidad en los ltimos aos presentndose como una nueva forma para que las empresas se puedan comunicar con sus clientes, tanto potenciales como reales; adems que aprovechan la progresiva popularidad de las aplicaciones de telefona celular.
            .col-md-2.col-lg-4.col-4
              img(src='@/assets/curso/temas/tema3/ps1-5.png', alt='')
          .row.align-items-center.justify-content-center(numero="6" titulo= "WhatsApp")
            .col-md-10.col-lg-7.col-sm-12 
              p Herramienta que posee un gran potencial para que las empresas tengan una comunicacin directa con los clientes. Es una aplicacin que brinda una funcionalidad importante para las empresas (WhatsApp Business) para que estas puedan tener un contacto ms gil con sus clientes a travs de un chatbot que permite a la marca responder de manera instantnea a una gran cantidad de contactos diariamente. Este #[em chatbot] es muy til para empresas que reciben masivamente contactos en el da, adems de que puede direccionar las consultas a cada rea a travs de l. 
            .col-md-2.col-lg-4.col-4
              img(src='@/assets/curso/temas/tema3/ps1-6.png', alt='')

    separador
    #t_3_2.titulo-segundo.mb-5
      h2 3.2 El asesor comercial

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema3/img3.png', alt='')
      .col-lg-8.col-md-12.col-sm-12
        p Profesional cuya funcin principal es proporcionar informacin completa y detallada a clientes potenciales con el objetivo de facilitar una  decisin de compra. Entre otras responsabilidades, este profesional debe conocer los mercados, productos y servicios, as como realizar un seguimiento continuo de los clientes o. posibles compradores (Wetto, 2017).
        p El asesor comercial, en general, es un vendedor, pero no se limita a realizar una venta. Su objetivo es obtener de los clientes la informacin necesaria para ofrecerles el producto que mejor se ajuste a sus necesidades. En este sentido, brindan atencin antes, durante y despus de la compra. Desempean un rol de doble va: adems de asesorar y proporcionar informacin til a los clientes, recopilan datos importantes que la empresa puede utilizar para mejorar en todos los aspectos, desde productos y servicios hasta estrategias comerciales, branding y mercado objetivo.

    .mb-4.d-flex.ps-lg-0
      .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
        h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") Funciones del asesor comercial
        img.icon(src='@/assets/curso/temas/icon.svg') 

    p.mb-4 El asesor comercial cuenta con muchos elementos y herramientas para el logro de su objetivo, la venta, por lo cual es necesario conocer las funciones inherentes a su cargo, las cuales se exponen a continuacin :

    .row
      .col-sm-12.col-md-12.col-lg-12
        .tarjeta.tarjeta--rosada.p-4
          LineaTiempoC.color-acento-contenido(text-small)
            .row.justify-content-center.align-items-center(titulo="1")
              .col-md-3.col-lg-3.col-6
                img.mb-2(src='@/assets/curso/temas/tema3/ps2-1.png', alt='Texto que describa la imagen')
              .col-md-9.col-lg-7.col-sm-12
                h4.mb-4  Conocer ampliamente el mercado
                p Esta funcin abarca no solo poseer datos precisos sobre la esencia de la venta, sino tambin sobre desarrollar una investigacin de las necesidades reales de los clientes, para ofrecerles el producto o servicio que mejor se adece a sus necesidades o deseos. Tambin permite al asesor comercial descubrir potenciales clientes, conociendo en dnde se encuentran y cmo contactarlos.
            .row.justify-content-center.align-items-center(titulo="2")
              .col-md-3.col-lg-3.col-6
                img.mb-2(src='@/assets/curso/temas/tema3/ps2-2.png', alt='Texto que describa la imagen')
              .col-md-9.col-lg-7.col-sm-12
                h4.mb-4  Conocer el producto o servicio que ofrece
                p Todo asesor comercial debe probar el producto o servicio que ofrece ponindose por un instante en la piel del consumidor. As es como el asesor puede estar en capacidad de dar respuesta a cualquier pregunta o duda que tenga el cliente, y llegar incluso a anticiparse, proporcionndole la informacin puntual que l est requiriendo.
            .row.justify-content-center.align-items-center(titulo="3")
              .col-md-3.col-lg-3.col-6
                img.mb-2(src='@/assets/curso/temas/tema3/ps2-3.png', alt='Texto que describa la imagen')
              .col-md-9.col-lg-7.col-sm-12
                h4.mb-4  Ser un asesor activo
                p Esta funcin es la que hace la diferencia entre un vendedor de un asesor comercial. El asesor no se limita en responder preguntas del cliente, sino que intenta indagar sobre sus necesidades, escuchndolo con atencin para ofrecerle la mejor opcin, aunque esta pueda ser completamente distinta a la que en principio el cliente buscaba.
            .row.justify-content-center.align-items-center(titulo="4")
              .col-md-3.col-lg-3.col-6
                img.mb-2(src='@/assets/curso/temas/tema3/ps2-4.png', alt='Texto que describa la imagen')
              .col-md-9.col-lg-7.col-sm-12
                h4.mb-4  Proporcionar informacin pertinente
                p Es una obligacin tener consigo toda la informacin sobre el producto. Esto lo puede lograr leyendo los instructivos y datos de los productos, pero tambin probndolos, sintiendo en carne propia los atributos y carencias, usos y precauciones. El vendedor busca el producto que el cliente le pidi. En cambio, el asesor comercial averigua sobre la necesidad que el comprador desea satisfacer al adquirirlo y, si es del caso, le puede sugerir una mejor eleccin, o simplemente ratifica la buena seleccin del producto por parte del cliente. Tambin debe estar enterado sobre competencia y sobre las caractersticas del mercado. Por ello, debe tener curiosidad, ser investigador y estar al da sobre tendencias, novedades, actualidad, lanzamiento de productos, artculos discontinuos, cambios en las polticas comerciales o de fabricacin, leyes, normas y todo aquello que pueda incidir o comprometer la venta.
            .row.justify-content-center.align-items-center(titulo="5")
              .col-md-3.col-lg-3.col-6
                img.mb-2(src='@/assets/curso/temas/tema3/ps2-5.png', alt='Texto que describa la imagen')
              .col-md-9.col-lg-7.col-sm-12
                h4.mb-4  Mantener una comunicacin eficiente
                p Tambin es obligatorio tener los instrumentos adecuados para tener una excelente comunicacin, tanto oral como escrita, con el comprador. Adems de entenderlo y escucharlo, debe aplicar la psicologa para conseguir informacin relevante con el fin de ofrecerle la mejor eleccin o, si es necesario, hacerlo cambiar de opinin. Por eso, esta funcin tambin trae consigo el ser perceptivo y el ser comprensivo.
            .row.justify-content-center.align-items-center(titulo="6")
              .col-md-3.col-lg-3.col-6
                img.mb-2(src='@/assets/curso/temas/tema3/ps2-6.png', alt='Texto que describa la imagen')
              .col-md-9.col-lg-7.col-sm-12
                h4.mb-4  Tener una actitud proactiva
                p Debe adelantarse a las necesidades del cliente, siempre pensando en su satisfaccin y en la solucin de sus problemas. Esto lo logra siempre y cuando lo conozca bien, sabiendo qu es lo que necesita y por qu lo busca, llevando un historial de sus compras y obteniendo datos relevantes como, por ejemplo, qu productos ha adquirido, cul es el estilo de vida que tiene, cules son sus gustos e intereses, cmo se compone su ncleo familiar, y todos los datos que pueda obtener de l. Es as como el asesor comercial podr estar en condiciones de anticiparse al cliente para poderle ofrecer beneficios o variedad de elecciones hasta que descubra lo que realmente requiere, como, por ejemplo, nuevos productos o nuevas versiones de artculos que ya tiene.
            .row.justify-content-center.align-items-center(titulo="7")
              .col-md-3.col-lg-3.col-6
                img.mb-2(src='@/assets/curso/temas/tema3/ps2-7.png', alt='Texto que describa la imagen')
              .col-md-9.col-lg-7.col-sm-12
                h4.mb-4  Hacer seguimiento de los clientes
                p Una de las funciones ms importantes es hacer un seguimiento exhaustivo de los clientes, identificando a los compradores insatisfechos  que no encontraron en el mercado el producto que satisfaga sus necesidades o deseos. Es crucial mantener la lealtad de los clientes fieles y captar a los potenciales, motivndolos a realizar una compra y familiarizarse con los productos de la marca. Para lograrlo, es esencial administrar una base de datos actualizada que permita al asesor comercial contactar tanto a clientes actuales como potenciales. Esta base de datos debe incluir informacin como nmeros telefnicos y correos electrnicos para enviarles noticias y ofertas de su inters.
            .row.justify-content-center.align-items-center(titulo="8")
              .col-md-3.col-lg-3.col-6
                img.mb-2(src='@/assets/curso/temas/tema3/ps2-8.png', alt='Texto que describa la imagen')
              .col-md-9.col-lg-7.col-sm-12
                h4.mb-4  Conjugar intereses
                p En una accin de compra intervienen tres actores: el cliente, el vendedor y el producto que se promociona. Como en toda relacin comercial, el objetivo es que todos los actores obtengan un beneficio: el comprador satisface su necesidad, el vendedor recibe una comisin por la venta, y el producto contribuye a la rentabilidad de la empresa. Para que esta relacin comercial perdure en el tiempo, es esencial que el asesor comercial, quien acta como puente entre la empresa y el cliente, mantenga satisfechos a ambos a travs de una excelente atencin. Esto se logra mediante una comunicacin honesta y clara entre los tres actores (Wetto, 2017).

    Separador
    #t_3_3.titulo-segundo.mb-5
      h2 3.3 Medios de comunicacin impresos

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema3/img4.png', alt='')
      .col-lg-8.col-md-12.col-sm-12
        p Son aquellos que al producirse deben pasar por un proceso de impresin, siendo los peridicos, las revistas y los folletos algunos de los que ms se destacan. Sin embargo, dentro de estos, y adems de los nombrados anteriormente, se encuentran otras publicaciones en papel, como, por ejemplo, los libros, plegables, carteles, afiches, lonas y anuncios. Su finalidad es la informacin que deben entregar a los clientes, sobre temas de su inters; adems de motivar, educar, construir relaciones y generar una primera compra por parte del cliente.
        .cajon.color-primario.p-4.mb-4
          p Estos medios dependen de un proceso de distribucin que se debe planear de manera estratgica para hacer que el pblico tenga conocimiento de marca, apoyando al anunciante en la intencin de persuadir al cliente a realizar una accin de compra. Algunos de estos medios impresos salen a la venta al pblico y se distribuyen en tiendas, papeleras, libreras, etc. 
        p Tambin existen  los comunicados institucionales, o sea que se distribuyen al interior de la empresa e incluso se pueden repartir entre sus clientes, tales como los folletos ilustrativos empresariales y los plegables, entre otros.

    p.mb-4 A pesar de las predicciones de que los medios electrnicos suplantarn a los medios impresos, estos an continan activos. Los medios impresos tienen excelentes resultados de impresin, y se puede decir que son los medios ms tangibles, que ayudan a la promocin, recordacin y posicionamiento de una marca y sus productos o servicios:

    .row.fondo1.align-items-center.p-3(data-aos="fade-down")
      .col-lg-12.col-md-12.col-12.px-lg-5.px-4
        .row.justify-content-center.mb-4
        
        .tarjetaf.p-3.mb-4
          SlyderA(tipo="b").slider-a-custom(data-aos="zoom-in-left")
            .row.justify-content-center.align-items-center.p-4
              .col-lg-7.col-md-12.col-sm-12
                h4.mb-4 Peridicos
                p Usa la comunicacin escrita y visual. Simboliza una oportunidad seductora para los anunciantes con presencia en mercados locales, departamentales o nacionales.
                p Es el medio  de comunicacin masivo que posee mayor credibilidad, porque la noticia tiene tiempo de ser revisada y estudiada antes de ser llevada a impresin. 
                p Son econmicos para el consumidor, pero no lo son para los anunciantes. En general, todos los medios de comunicacin masiva son costosos publicitariamente hablando. Adems, el papel peridico no es de excelente calidad, por lo que el proceso de impresin se debe realizar en tintas RGB (#[em Red , Green y Blue]).  
              .col-lg-4.col-md-6.col-9
                img(src="@/assets/curso/temas/tema3/sly1-1.png")
            .row.justify-content-center.align-items-center.p-4
              .col-lg-7.col-md-12.col-sm-12
                h4.mb-4 Revistas
                p As como los peridicos, las revistas tambin son medios masivos visuales, con la ventaja de poder seleccionar mejor a la audiencia meta (hay revistas especializadas en muchas reas, por ejemplo, deportivas, econmicas, farndula, noticias, tecnologa, etc.). 
                p Tienen mayor calidad tanto en impresin (usa tintas CMYK  cian, magenta, #[em yellow y black]) como en el papel (propalcote), por lo que son ms costosas para los lectores y tambin para los anunciantes.  
              .col-lg-4.col-md-6.col-9
                img(src="@/assets/curso/temas/tema3/sly1-2.png")
            .row.justify-content-center.align-items-center.p-4
              .col-lg-7.col-md-12.col-sm-12
                h4.mb-4 Folletos
                p Son de menor tamao que los medios antes mencionados y tambin estn basados en una comunicacin escrita. 
                p Se usan de manera interna para permitir el acercamiento directo con empleados y con clientes reales y potenciales pues en la mayora de los casos se entregan de manera directa. La marca que los enva a impresin (en este caso, anunciante), brinda a travs de este medio una informacin completa y extensa sobre las caractersticas y beneficios del producto. En comparacin con los peridicos y revista tienen bajo costo por impresin en volumen (Arellano, Barrera, Buenda, Castillo y Zamora, 2013).
              .col-lg-4.col-md-6.col-9
                img(src="@/assets/curso/temas/tema3/sly1-3.png")

    separador
    #t_3_4.titulo-segundo.mb-5
      h2 3.4 Muestra comercial

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-8.col-md-12.col-sm-12
        p Es una herramienta muy efectiva de #[em Branding], que en la actualidad puede ser usada de manera estratgica en las tiendas #[em online] o #[em e-commerce]. Estrategia tradicional de #[em marketing] que tambin es llamada #[em Sampling] y que radica en el envo de una muestra de productos de manera gratuita y con un claro objetivo promocional (Florido, 2021).
        p La muestra gratis se enva al lugar de ubicacin donde se encuentra, bien sea el cliente real y habitual, o el cliente potencial. Cuando se trata del cliente real y habitual, lo que se busca primordialmente es fidelizarlo para que l termine referenciando el producto a sus contactos, es decir, lo que se intenta hacer es incentivar el tradicional voz a voz, que en realidad es la mejor promocin que una marca puede tener, porque los clientes confan ms en las sugerencias de alguien conocido que en la misma publicidad. Y  Cuando se trata de un cliente potencial, lo que se busca es que lo conozca para que ms adelante termine comprndolo.
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema3/sly1-4.png', alt='')

    .row.justify-content-center.align-items-center
      .col-lg-8.col-md-12.col-sm-12
        .cajon.color-primario.p-4.mb-4
          p.p-3 Por eso, la estrategia de #[em marketing] que se relaciona con el envo de las muestras gratis debe estar bien pensada porque no se trata de enviar cualquier objeto. Se trata de cuidar muy bien la imagen de la marca eligiendo muy bien el artculo de regalo para generar un impacto significativo en el cliente.  

    .row.justify-content-center.align-items-center
      .col-lg-12.col-md-12.col-sm-12
        .titulo-nuevo.mt-2
          | 
          h4 Ejemplos ms conocidos de muestras gratuitas

    .row.fondo2.align-items-center(data-aos="fade-down")

          SlyderF(columnas="col-lg-12 col-md-12 col-12")
            .tarjeta.tarjeta--slyder(:style="{'background-image': `url(${require('@/assets/curso/temas/tema3/sly2-1.png')})`}").borde-recto.mt-2
              .row(titulo="xxx").mt-4
                .row.mt-5
                  .row.mt-5.mb-5
                  .row.mt-5.mb-5
                  .row.mt-5.mb-5
                  .row.mt-2
                  .col-lg-6.mt-2.bgblanco
                    h4.mt-2.ps-2 Muestras gratis para bebs
                    p.ps-2 Los padres saben que antes de nacer el beb es muy probable que comiencen a recibir muestras gratuitas de revistas para beb, cremas, paales, lociones, ropa, etc. Muy importante, que el cliente reciba la muestra en el momento idneo y preciso, que es en el momento del nacimiento del beb.        
            .tarjeta.tarjeta--slyder(:style="{'background-image': `url(${require('@/assets/curso/temas/tema3/sly2-2.png')})`}").borde-recto
              .row(titulo="xxx").mt-4
                .row.mt-5
                  .row.mt-5.mb-5
                  .row.mt-5.mb-5
                  .row.mt-5.mb-5
                  .row.mt-3
                  .col-lg-6.mt-5.bgblanco
                    h4.mt-2.ps-2 Muestras gratis de maquillaje
                    p.ps-2 Este es otro excelente ejemplo de esta estrategia publicitaria que una marca puede hacer, pues el maquillaje es un artculo pequeo que no ocupa mucho espacio y, adems, es fcil de enviar por correo directo.
            .tarjeta.tarjeta--slyder(:style="{'background-image': `url(${require('@/assets/curso/temas/tema3/sly2-3.png')})`}").borde-recto
              .row(titulo="xxx").mt-4
                .row.mt-5
                  .row.mt-5.mb-5
                  .row.mt-5.mb-5
                  .row.mt-5.mb-4
                  .row.mt-3
                  .col-lg-6.mt-5.bgblanco
                    h4.mt-2.ps-2 Muestras gratis de perfumes
                    p.ps-2 En este caso es muy usual en clientes potenciales porque la idea es regalarles una muy pequea muestra para que lo conozcan. Esta estrategia contina siendo efectiva porque la mayora de clientes, adems de aceptar la muestra, agradecen el detalle.
            .tarjeta.tarjeta--slyder(:style="{'background-image': `url(${require('@/assets/curso/temas/tema3/sly2-4.png')})`}").borde-recto
              .row(titulo="xxx").mt-4
                .row.mt-5
                  .row.mt-5.mb-5
                  .row.mt-5.mb-5
                  .row.mt-5.mb-3
                  .col-lg-6.mt-5.bgblanco
                    h4.mt-2.ps-2 Demos gratis de los videojuegos
                    p.ps-2 Esta industria tambin ha comprendido el potencial que tiene ofrecer muestras gratuitas, porque as motivan la compra del videojuego ms adelante. En la actualidad, esta muestra puede ser online, descargando una versin gratuita de internet; el cliente queda con deseos de obtener la versin completa. Estrategia que funciona muy bien.
            .tarjeta.tarjeta--slyder(:style="{'background-image': `url(${require('@/assets/curso/temas/tema3/sly2-5.png')})`}").borde-recto
              .row(titulo="xxx").mt-4
                .row.mt-5
                  .row.mt-5.mb-5
                  .row.mt-5.mb-3
                  .col-lg-6.mt-5.bgblanco
                    h4.mt-2.ps-2 Plantillas gratuitas Wordpress y Wix
                    p.ps-2 Son dos plataformas de plantillas gratuitas que ya tienen mltiples opciones de diseos de sitios #[em web] y #[em blogs], llamativos e impactantes visualmente, y que estn perfectamente diseados por categoras (tecnologa, restaurantes, viajes, moda, servicios, etc.). El cliente puede elegir una plantilla para su sitio #[em web], probarla, editarla y personalizarla. Y cuando est seguro de su diseo y tenga comprados el dominio y el #[em hosting] en internet, procede a pagar a dichas plataformas la plantilla. Es negocio asegurado cuando se trata de una empresa o una persona que primero desea ver cmo queda su diseo de sitio #[em web] o #[em blog].
          .row.mt-5

    p.mb-4.mt-4 La estrategia consiste entonces en que las empresas entreguen muestras gratis de sus productos con la finalidad de fidelizar los clientes reales y habituales o de alcanzar clientes potenciales que se interesen por lo que la empresa ofrece. Cuando el cliente prueba un producto, sea cual sea, tiene ms posibilidades de terminar comprndolo, siempre y cuando le haya gustado.

    .row.justify-content-center.align-items-center
      .col-lg-12.col-md-12.col-sm-12
        .titulo-nuevo.mt-2
          | 
          h4 Existen varias formas de ofrecer estas muestras:

    .row.justify-content-center.align-items-center
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema3/img5.png', alt='')
      .col-lg-8.col-md-12.col-sm-12
        ul.lista-ul--color.color-acento-contenido.fa-ul.mt-4
          li 
            i.fas.fa-check-circle
            span.mb-0 Ofrecerlo como regalo por la compra de otro artculo.
          li 
            i.fas.fa-check-circle
            span.mb-0 Enviando muestras gratuitas a domicilio; los gastos de envo son pagados por la marca. En este caso se puede hacer de dos formas; cuando la marca elige los clientes reales y habituales para enviarles la muestra gratis; y cuando la empresa da la oportunidad (por ejemplo, en #[em internet]) de que los clientes potenciales soliciten la muestra gratuita.
          li 
            i.fas.fa-check-circle
            span.mb-0 Ofrecer la muestra gratuita en ferias comerciales (Florido, 2021). 

    separador
    #t_3_5.titulo-segundo.mb-5
      h2 3.5 Material publicitario en el punto de venta

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-2.col-md-3.col-3
        img(src='@/assets/curso/temas/tema3/img6.png', alt='')
      .col-lg-10.col-md-12.col-sm-12
        p El concepto de material POP proviene de #[em Point Of Purchase], por sus siglas en ingls; su traduccin directa es punto de compra; que se refiere al material situado en los lugares donde se exhiben los productos con el fin de atraer la atencin del cliente para motivarlos a una accin de compra (Yate, 2021).
        p El material POP, tambin se conoce como Publicidad en Lugar de Venta (PLV), o publicidad en el punto de venta. Este material tiene una amplia variedad de formas y tamaos, como, por ejemplo, exhibidores, rompe trficos, habladores y puntas de gndola, entre otros. 

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-8.col-md-12.col-sm-12
        p Las caractersticas ms importantes que debe tener el material POP son: 
        ul.lista-ul--color.color-acento-contenido.fa-ul.mt-4
          li 
            i.fas.fa-check-circle
            span.mb-0 Estar situado en el punto de venta del artculo al que se le hace publicidad o promocin.
          li 
            i.fas.fa-check-circle
            span.mb-0 Tener medidas coherentes con el rea disponible en el punto de venta, tanto en dimensiones como en costos de fabricacin. 
          li 
            i.fas.fa-check-circle
            span.mb-0 Poseer un diseo publicitario que refuerce la identidad corporativa de la empresa y sus productos promocionados. 
          li 
            i.fas.fa-check-circle
            span.mb-0 Lograr el posicionamiento de la marca y sus productos en el punto de venta. 
          li 
            i.fas.fa-check-circle
            span.mb-0 Responder a condiciones de tendencias, fechas especiales y modas.
      .col-lg-4.col-md-6.col-4
        img(src='@/assets/curso/temas/tema3/img7.png', alt='')

    .mb-4.d-flex.ps-lg-0
      .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
        h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") Tipos de material publicitario en el punto de venta
        img.icon(src='@/assets/curso/temas/icon.svg') 

    p.mb-4 A continuacin, se invita a interactuar para conocer diferentes tipos de material publicitario utilizado en el punto de venta:

    .row.justify-content-center.align-items-center
        .col-lg-12.col-md-12.col-sm-12
          ImagenInfograficaB.color-secundario
            template(v-slot:imagen)
              figure
                img(src='@/assets/curso/temas/tema3/pc1.png', alt='Texto que describa la imagen')

            div(x="70%" y="14%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Exhibidor
                    p Exponen los productos en diferentes zonas del establecimiento, que van desde el mostrador hasta los pasillos, permitiendo que los artculos se destaquen. 
                    p Originan la interaccin con el cliente, haciendo que se estimule en el cliente una experiencia de compra personalizada, porque le deja analizar que el producto satisface una necesidad, permitindole ver, tocar y en ocasiones probar el producto. Al utilizar exhibidores POP, se pueden rentabilizar los espacios vacos existentes en el establecimiento, ya que se aprovechan zonas donde las gndolas no pueden llegar.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-1.png', alt='')

            div(x="78%" y="31%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Rompetrfico
                    p Es una pieza publicitaria que impide, separa o transgrede la circulacin de los consumidores en el punto de venta. Su objetivo es llamar la atencin para que mejore la percepcin de los clientes acerca de los productos exhibidos, a la vez que exhibe sus caractersticas.
                    p Algunas de sus principales caractersticas son:
                    ul.lista-ul--color.color-secundario.fa-ul.mt-4
                      li 
                        i.fas.fa-check-circle
                        span.mb-0 Tiene grficos que refuerzan la identidad corporativa.
                      li 
                        i.fas.fa-check-circle
                        span.mb-0 Refuerza el posicionamiento y lugar del producto en el establecimiento.
                      li 
                        i.fas.fa-check-circle
                        span.mb-0 Hay varios tipos de rompe trfico y su clasificacin depende del lugar en el punto de venta: de piso, de pared, colgantes, en gndola y corredores.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-2.png', alt='')

            div(x="80%" y="50%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Hablador
                    p Pieza publicitaria que enuncia las caractersticas de los productos exhibidos. Las siguientes son sus caractersticas:
                    ul.lista-ul--color.color-secundario.fa-ul.mt-4
                      li 
                        i.fas.fa-check-circle
                        span.mb-0 Refuerza la identidad corporativa.
                      li 
                        i.fas.fa-check-circle
                        span.mb-0 Posiciona y mejora la exposicin de los productos en el establecimiento.
                      li 
                        i.fas.fa-check-circle
                        span.mb-0 Se puede encontrar en lnea de cajas, gndolas, vitrinas, la pared y colgando sobre los pasillos de los supermercados.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-3.png', alt='')

            div(x="33%" y="12%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Punta de gndola
                    p Espacio que se designa en los dos extremos de un lineal o gndola para exhibir los productos de una manera ms visible e impactante, de tal forma que aumente la comunicacin visual porque tiene la posibilidad de tener accesorios publicitarios tales como cabezotes, laterales, frontales, cenefas y partes mviles. Su objetivo es aumentar la visibilidad y posicionar la marca en el punto de venta, por lo que son muy tiles y perfectos para hacer diseo personalizados.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-4.png', alt='')

            div(x="51%" y="29%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 #[em Dummy]
                    p Maqueta o modelo a escala natural del producto con el objetivo de apoyar la decisin de compra por parte del cliente. Es una de las formas ms efectivas para informar sobre una marca, un producto o un servicio porque presenta una gran versatilidad en diseos, tamaos y conceptos, simulando cualquier tipo de objeto o personaje que se requiera para representar ideas creativas, como, por ejemplo, la mascota y la forma del producto, entre otras. Deben ser una representacin muy parecida al original de tal forma que pueda impactar visualmente al cliente.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-5.png', alt='')

            div(x="69%" y="48%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Cenefa
                    p Pieza publicitaria que se usa en el punto de venta para beneficiar a las marcas ubicndose de manera perpendicular a los entrepaos de las gndolas o lineales y de frente al producto exhibido para destacar sus cualidades.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-6.png', alt='')

            div(x="25%" y="26%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Lateral
                    p Por lo general se usan como exhibidores que permiten captar la atencin del cliente dentro del establecimiento comercial, y si posee un diseo y un mensaje contundentes es vital para que el producto mejore su percepcin.
                    p Normalmente se encuentra ubicado en la entrada de un supermercado, en la zona de cajas, en las gndolas o lineales, en las vitrinas de congelados, colgando en los pasillos y en los muros del establecimiento. Sus caractersticas y formatos deben ser coherentes con los espacios disponibles o el diseo de la estrategia. Pero lo ms importante es que el mensaje posicione la marca, sus beneficios y su diferencia competitiva.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-7.png', alt='')

            div(x="36%" y="61%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Mvil
                    p Instrumentos promocionales que se disean para descolgarse del techo o de algn exhibidor de gran altura para suministrar informacin promocional o de ubicacin de productos y servicios.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-8.png', alt='')

            div(x="25%" y="64%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 #[em Sticker]
                    p Tambin se conoce con el nombre de adhesivo. Es un impreso publicitario o promocional que puede ser adherido sobre los propios productos, o incluso sobre otros materiales publicitarios. Pueden resaltar una promocin, precio o producto en el stock. Puede destacar una oferta, informar novedades que posea un producto y sirve para cautivar la atencin del cliente. Su tamao es menor al de los dems materiales POP, pero su diseo y formato resultan provechosos, porque an as la marca puede informar al cliente sobre datos de su inters, y al estar pegado sobre producto, ayuda a aumentar la intencin de compra.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-9.png', alt='')

            div(x="50%" y="62%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 #[em Brochure]
                    p Pieza publicitaria que tiene forma de folleto o documento grfico e informativo que exhibe informacin relevante sobre la marca que es de valor para los clientes. Desde la teora, un brochure empresarial incluye materiales publicitarios como afiches, trpticos (plegables de tres cuerpos) y carpetas, entre otros. Estas piezas se pueden crear para uso interno o externo de la marca, es decir, pueden ir dirigidos a los empleados, as como tambin, puede ser diseados para informar a los clientes. Esta pieza puede ser diseada de manera digital para ser compartida con su pblico a travs del correo electrnico, redes sociales e incluso el WhatsApp.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-10.png', alt='')

            div(x="66%" y="78%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Embalajes presentadores
                    p Materiales o recipientes que sirven para la contencin de un grupo de productos para exhibirlos y ponerlos en venta al pblico.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-11.png', alt='')

            div(x="19%" y="47%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 #[em Displays]
                    p Son soportes independientes que pueden ser de cartn, metal, madera, tela, plstico, alambre, y que pueden exhibir varios productos. Pueden ir ubicados en escaparates (vitrina externa) como tambin dentro del punto de venta en zonas estratgicas donde sean visibles al cliente.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-12.png', alt='')

            div(x="46%" y="13%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Carteles
                    p Es el ms bsico de la publicidad dentro del establecimiento. Es un elemento primario porque se requiere para informar todo tipo de mensajes, desde la presencia de un producto, hasta destacar precios, descuentos, promociones, etc. 
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-13.png', alt='')

            div(x="58%" y="12%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Megafona publicitaria
                    p Son los anuncios que se escuchan dentro del establecimiento y son emitidos por un medio radiofnico mientras los clientes hacen sus compras.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-14.png', alt='')

            div(x="39%" y="28%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Proyecciones audiovisuales
                    p Son videos publicitarios que se emiten por medios audiovisuales como por ejemplo televisores de gran tamao para llamar la atencin del cliente y motivarlos a hacer una compra. Se ubican en zonas estratgicas del establecimiento donde el cliente puede permanecer cierto tiempo como, por ejemplo, una zona de pago.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-15.png', alt='')

            div(x="30%" y="43%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Punto de degustacin
                    p Expositor que tambin se sita por separado de los dems exhibidores y que tambin interfiere la circulacin de los clientes dentro del establecimiento, pero que promueve una muestra gratuita del producto para que lo pruebe y lo tenga en cuenta a la hora de hacer la compra, a travs de un impulsor con el fin de apreciar su calidad. Sus funciones son las de presentar al pblico un producto en un punto de venta, llamar la atencin de los clientes, hacer que el consumidor tenga el producto en la mano y lo pruebe con el fin de ayudar en su percepcin por parte de los clientes.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-16.png', alt='')

            div(x="75%" y="66%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 #[em Flanger]
                    p Pieza publicitaria que sirve como placa con el nombre o distintivo visual de un producto o marca. Su funcin es la de comunicar y captar la atencin de los usuarios a lo lejos sobre la presencia de un producto dentro del almacn con el objetivo de ayudar en la percepcin de este en los clientes.
                  .col-lg-3
                    img(src='@/assets/curso/temas/tema3/pc1-17.png', alt='')

            div(x="56%" y="47%" tooltip="Ver ms" numero="+")
              .tarjeta.white-card.p-4
                .row.align-items-center.justify-content-center
                  .col-lg-6
                    .h4 Saltarn
