        p La omnicanalidad busca ofrecer una experiencia de cliente cohesiva y significativa, independientemente del canal utilizado, ya sea tradicional o digital. Esta estrategia, ampliamente aceptada por empresas y estrategas de #[em marketing], an cuenta con pocos casos de implementacin completa debido a su novedad (Romero, Maldonado y Nez, 2021).
