        p En la actualidad, las empresas deben atraer a clientes cada vez ms digitales, quienes han adoptado rpidamente las nuevas tecnologas y, gracias a la difusin de la telefona celular, estn acostumbrados a disponer de lo que desean, cuando y donde quieran. La omnicanalidad, por tanto, debe ser flexible y conveniente, permitiendo que los clientes se comuniquen con las marcas a travs del canal que elijan, en cualquier momento y lugar.
